.headerContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid var(--lighgrey);
    height: 8vh;
    margin-bottom: 2rem;
}

.inputContainer {
    width: 50%;
    padding-left: 3rem;
}

.input {
    width: 50%;
    border: none;
    background-color: var(--grey);
    padding: 0.1rem 0.6rem;
    padding-left: 0.8rem;
    border-radius: 30px;
    color: white;
    font-size: 0.5rem
    /* var(--small-font); */
}

.input::placeholder {
    color: white;
    font-size: 0.4rem;
}

.searchIcon {
    background-color: transparent;
    background-size: 0px;
    backdrop-filter: inherit;
}

.headerDetailsContainer {
    display: flex;
    width: 50%;
    justify-content: flex-end;
    align-items: center;
    padding-right: 4rem;
}

.notificationContainer {
    position: 'relative';
    /* margin: 0px 1rem; */
}

.numOfNotification {
    display: inline;
    font-size: 0.5rem;
}

/* LANGUAGE OPTIONS */
select {
    border: none;
    font-weight: 600
}

option {
    font-size: var(--small-font);
}

.langOptionContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1.5rem;
}
/* LANGUAGE OPTIONS END */

/* VERTIVAL RULE */
.hr {
    width: 1.2rem;
    background-color: var(--grey-font);
    margin: 0px 0.9rem;
    transform: rotate(90deg);
}
/* VERTICAL RULE END */

/* USERNAME AND ADMIN STATUS */
.usernameContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    margin-right: 1rem;
}

.username {
    font-size: var(--medium-font);
    font-weight: 600;
}

.rank {
    font-size: var(--small-font);
    color: var(--grey-font);
}

/* USERNAME AND ADMIN STATUS END */

.profilePicContainer {
    height: 1.8rem;
    width: 1.8rem;
    border-radius: 50%;
    background-color: plum;
    display: flex;
    justify-content: center;
    align-items: center;
}

.userInitials {
    background-color: transparent;
    color: black;
    text-align: center;
    font-weight: 500;
}

@media (max-width: 700px) {
    .inputContainer {
        width: 70%;
        padding-left: 4rem;
    }

    .username {
        font-size: 0.65rem;
        font-weight: 600;
    }

    .rank {
        font-size: 0.6rem;
        color: var(--grey-font);
    }

    .input {
        width: 70%;
        font-size: 0.5rem
    }

    .headerDetailsContainer {
        display: flex;
        width: 70%;
        justify-content: flex-end;
        align-items: center;
        padding-right: 2rem;
    }
}

@media (max-width: 600px) {
    .userInitials {
        font-size: 0.55rem;
   }

   .profilePicContainer {
       height: 1.2rem;
       width: 1.2rem;
    }

    .input {
        padding: 0.1rem 0.6rem;
        font-size: 0.1rem;
    }

    .input::placeholder {
        color: white;
        font-size: 0.1rem;
    }
}

@media (max-width: 570px) {
    .profilePicContainer {
        display: none;
    }
}

@media (max-width: 540px) {
    .hr {
        margin: 0px 0.2rem;
    }

    .langOptionContainer {
        margin-left: 1rem;
    }

    .headerDetailsContainer {
        display: flex;
        width: 70%;
        justify-content: flex-end;
        align-items: center;
        padding-right: 1rem;
    }
}

@media (max-width: 500px) {
    .hr {
        margin: 0px 0.2rem;
    }

    .langOptionContainer {
        margin-left: 1rem;
    }

    .inputContainer {
        width: 40%;
        padding-left: 4rem;
    }

    .headerDetailsContainer {
        padding-right: 1rem;
        width: 50%;
    }

    option {
        font-size: 0.6rem;
    }

    .langOptionContainer {
        margin-left: 1.2rem;
    }
}

@media (max-width: 419px) {
    .hr {
        margin: 0px 0.1rem;
    }

    .langOptionContainer {
        margin-left: 0.8rem;
    }

    .inputContainer {
        width: 40%;
        padding-left: 3rem;
    }

    .headerDetailsContainer {
        padding-right: 1rem;
        width: 50%;
    }

    option {
        font-size: 0.6rem;
    }

    .langOptionContainer {
        margin-left: 1.2rem;
    }
}

@media (max-width: 390px) {
    .hr {
        margin: 0px 0.1rem;
    }

    .langOptionContainer {
        margin-left: 0.5rem;
    }

    .inputContainer {
        width: 35%;
        padding-left: 2.5rem;
    }

    .headerDetailsContainer {
        padding-right: 1rem;
        width: 50%;
    }

    option {
        font-size: 0.6rem;
    }

    .langOptionContainer {
        margin-left: 1.2rem;
    }
}